import React, { useState, useEffect } from "react";
import "./App.css";
import LoginPage from "./comp/LoginPage";
import Main from "./comp/Main";
import io from "socket.io-client";

export const DataContext = React.createContext(null);

const socket = io("/");

const App = () => {
  const [userInfo, setUserInfo] = useState({
    name: "Victorowsky",
    userType: "admin",
    image:
      "https://lh3.googleusercontent.com/ogw/ADGmqu-KSBqyf3CGioDMikcgoA6s1Sp0RZfkOgrsdr9I=s32-c-mo",
  });

  useEffect(() => {
    fetch("/getProfile", { credentials: "include" })
      .then((res) => res.json())
      .then((res) => {
        if (res.profile) {
          console.log(res.profile);
          setUserInfo(res.profile);
        }
      });
  }, []);

  return (
    <div className="app">
      <DataContext.Provider value={{ userInfo, socket }}>
        {userInfo ? <Main /> : <LoginPage />}
      </DataContext.Provider>
    </div>
  );
};

export default App;
